#switch {
    position: absolute;
    right: 10px;
}

.card {
    margin: auto;
    margin-top: 50px;
    text-align: center;
    font-size: 16px;
    border: none;
}

.about_text {
    text-align: center;
    width: 800px;
    margin: auto;
    font-family: 'Kantumruy Pro', sans-serif;
    font-size: 19px;
}

.about_box {
    text-align: center;
}

.progress {}

.skills {
    margin: auto;
    text-align: center;
}

.skills_text {
    color: yellow;
}

span {
    color: yellow;
    text-align: center;
}

.progress {
    margin: auto;
}

.title_about {
    color: yellow;
}

#myPic img {
    width: 60px;
    border-radius: 10%;
    border: 10px solid rgb(153, 153, 153);
}

#name {
    color: black;
    margin-top: 100px;
}

#myImg {
    width: 240px;
    border-radius: 10px;
}

#bold {
    font-weight: 600;
    font-size: 22px;
    color: yellow;
}

.card-link {
    font-size: 22px;
    padding: 5px;
}

.timeline {
    border-left: 3px solid #727cf5;
    border-bottom-right-radius: 4px;
    border-top-right-radius: 4px;
    background: rgba(114, 124, 245, 0.09);
    margin: 0 auto;
    letter-spacing: 0.2px;
    position: relative;
    line-height: 1.4em;
    font-size: 1.03em;
    padding: 50px;
    list-style: none;
    text-align: left;
    max-width: 40%;
}

.heading {
    color: yellow;
}

.edu {
    padding-bottom: 100px;
}

@media (max-width: 767px) {
    .timeline {
        max-width: 98%;
        padding: 25px;
    }
    .about_text {
        width: 20em;
        text-align: justify;
        margin: auto;
    }
}

.timeline h1 {
    font-weight: 300;
    font-size: 1.4em;
}

.timeline h2,
.timeline h3 {
    font-weight: 600;
    font-size: 1rem;
    margin-bottom: 10px;
}

.timeline .event {
    border-bottom: 1px dashed #e8ebf1;
    padding-bottom: 25px;
    margin-bottom: 25px;
    position: relative;
}

@media (max-width: 767px) {
    .timeline .event {
        padding-top: 30px;
    }
}

.timeline .event:last-of-type {
    padding-bottom: 0;
    margin-bottom: 0;
    border: none;
}

.timeline .event:before,
.timeline .event:after {
    position: absolute;
    display: block;
    top: 0;
}

.timeline .event:before {
    left: -207px;
    content: attr(data-date);
    text-align: right;
    font-weight: 100;
    font-size: 0.9em;
    min-width: 120px;
}

@media (max-width: 767px) {
    .timeline .event:before {
        left: 0px;
        text-align: left;
    }
}

.timeline .event:after {
    -webkit-box-shadow: 0 0 0 3px #727cf5;
    box-shadow: 0 0 0 3px #727cf5;
    left: -55.8px;
    background: #fff;
    border-radius: 50%;
    height: 9px;
    width: 9px;
    content: "";
    top: 5px;
}

@media (max-width: 767px) {
    .timeline .event:after {
        left: -31.8px;
    }
}

.rtl .timeline {
    border-left: 0;
    text-align: right;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    border-bottom-left-radius: 4px;
    border-top-left-radius: 4px;
    border-right: 3px solid #727cf5;
}

.rtl .timeline .event::before {
    left: 0;
    right: -170px;
}

.rtl .timeline .event::after {
    left: 0;
    right: -55.8px;
}

@media screen and (max-width: 577px) {
    #switch {
        position: absolute;
        left: 10px;
        margin-top: 10px;
    }
}